import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse, HttpEventType, HttpEvent } from '@angular/common/http';
import { IUser } from '../shared/interface/IUser';
import { Observable, throwError } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';
import { GlobalService } from './global.service';

@Injectable({
  providedIn: 'root'
})

export class UserService {
  private usersUrl = 'api/User';
  private userByUserNameUrl = 'api/User/GetUserByUserName/';
  private activeUserByUserNameUrl = 'api/User/GetActiveUserByUserName/';
  private loaderUsersByTeamUrl = 'api/User/GetAllLoaderUsersByTeam';
  private userProfileUrl = 'api/UserProfile';

  constructor(private http: HttpClient, private globalService: GlobalService) {
    this.usersUrl = globalService.baseUrl() + this.usersUrl;
    this.userByUserNameUrl = globalService.baseUrl() + this.userByUserNameUrl;
    this.activeUserByUserNameUrl = globalService.baseUrl() + this.activeUserByUserNameUrl;
    this.loaderUsersByTeamUrl = globalService.baseUrl() + this.loaderUsersByTeamUrl;
    this.userProfileUrl = globalService.baseUrl() + this.userProfileUrl;
  }

  createUser(user: IUser): Observable<IUser> {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });

    return this.http.post<IUser>(this.usersUrl, user, { headers })
      .pipe(
        tap(data => console.log('All: ' + JSON.stringify(data))),
        catchError(this.handleError)
      );
  }

  getUserProfile(): Observable<IUser> {
    var tokenHeader = new HttpHeaders({ "Authorization": "Bearer " + localStorage.getItem("token") })

    return this.http.get<IUser>(this.userProfileUrl, { headers: tokenHeader })
      .pipe(
        catchError(this.handleError)
      );
  }

  getUserByUserName(userName: string): Observable<IUser> {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });

    return this.http.get<IUser>(this.userByUserNameUrl + userName, { headers: headers })
      .pipe(
        tap(data => console.log()),
        catchError(this.handleError)
      );
  }

  getActiveUserByUserName(userName: string): Observable<IUser> {
    const headers = new HttpHeaders({ "Content-Type": "application/json" });

    return this.http.get<IUser>(this.activeUserByUserNameUrl + userName, { headers: headers })
      .pipe(
        tap(data => console.log()),
        catchError(this.handleError)
      );
  }

  getAllLoaderUsersByTeam(): Observable<IUser[]> {
    var tokenHeader = new HttpHeaders({ "Authorization": "Bearer " + localStorage.getItem("token") })

    return this.http.get<IUser[]>(this.loaderUsersByTeamUrl, { headers: tokenHeader })
      .pipe(
        tap(data => console.log()),
        catchError(this.handleError)
      );
  }

  private handleError(err: HttpErrorResponse) {
    // in a real world app, we may send the server to some remote logging infrastructure
    // instead of just logging it to the console
    let errorMessage = '';
    if (err.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      errorMessage = `An error occurred: ${err.error.message}`;
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
    }
    console.error(errorMessage);
    return throwError(errorMessage);
  }
}
