import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';

@Injectable({
    providedIn: "root"
})

export class GlobalService {
    private userLoggedIn = new Subject<boolean>();
    public currentPage: string;
    public userFullName: string;

    constructor() {
        this.userLoggedIn.next(false);
        this.userFullName = "";
        this.currentPage = "";
    }

    //Authentication Checker
    public isAuthenticated(): boolean {
        var result = false;
        if (localStorage.getItem("token") != null) {
            result = true;
        }

        return result
    }

    public isFinancerAuthenticated(): boolean {
        var result = false;

        if (localStorage.getItem("token") != null && localStorage.getItem("UserType") == "Financer") {
            result = true;
        }

        return result
    }

    public isLoaderAuthenticated(): boolean {
        var result = false;

        if (localStorage.getItem("token") != null && localStorage.getItem("UserType") == "Loader") {
            result = true;
        }

        return result
    }

    public setCurrentPage(page: string): void {
        this.currentPage = page;
    }

    setUserLoggedIn(userLoggedIn: boolean) {
        this.userLoggedIn.next(userLoggedIn);
    }

    getUserLoggedIn(): Observable<boolean> {
        return this.userLoggedIn.asObservable();
    }

    public GetUserFullName(): string {
        return localStorage.getItem("fullName");
    }

    public baseUrl(): string {
        return ''
        // return 'https://localhost:44393/'
    }
}