import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpErrorResponse, HttpEventType, HttpEvent } from '@angular/common/http';
import { IUser } from '../shared/interface/IUser';
import { Observable, throwError } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';
import { GlobalService } from './global.service';
import { ITransactionDetails } from './interface/ITransactionDetails';

@Injectable({
    providedIn: 'root'
})

export class TransactionService {
    private transactionDetailsUrl = 'api/TransactionDetails';
    private getTransactionDetailsUrl = 'api/TransactionDetails';
    private getTransactionDetailsByUserIdUrl = 'api/TransactionDetails/GetTransactionDetailsByUserId';
    private getTransactionDetailsByDatesUrl = 'api/TransactionDetails/GetTransactionDetailsByDates/';
    private getTransactionDetailsByReferenceNoUrl = 'api/TransactionDetails/GetTransactionDetailsByReferenceNo/';

    constructor(private http: HttpClient, private globalService: GlobalService) {
        this.transactionDetailsUrl = globalService.baseUrl() + this.transactionDetailsUrl;
        this.getTransactionDetailsUrl = globalService.baseUrl() + this.getTransactionDetailsUrl;
        this.getTransactionDetailsByUserIdUrl = globalService.baseUrl() + this.getTransactionDetailsByUserIdUrl;
        this.getTransactionDetailsByDatesUrl = globalService.baseUrl() + this.getTransactionDetailsByDatesUrl;
        this.getTransactionDetailsByReferenceNoUrl = globalService.baseUrl() + this.getTransactionDetailsByReferenceNoUrl;
    }

    submitTransactionDetails(transactionDetails: ITransactionDetails): Observable<ITransactionDetails> {
        const tokenHeader = new HttpHeaders({ "Authorization": "Bearer " + localStorage.getItem("token") });

        return this.http.post<ITransactionDetails>(this.transactionDetailsUrl, transactionDetails, { headers: tokenHeader })
            .pipe(
                tap(data => console.log()),
                catchError(this.handleError)
            );
    }

    getTransactionDetails(): Observable<ITransactionDetails[]> {
        const tokenHeader = new HttpHeaders({ "Authorization": "Bearer " + localStorage.getItem("token") });

        return this.http.get<ITransactionDetails[]>(this.getTransactionDetailsUrl, { headers: tokenHeader })
            .pipe(
                tap(data => console.log()),
                catchError(this.handleError)
            );
    }

    getTransactionDetailsByUserId(): Observable<ITransactionDetails[]> {
        const tokenHeader = new HttpHeaders({ "Authorization": "Bearer " + localStorage.getItem("token") });

        return this.http.get<ITransactionDetails[]>(this.getTransactionDetailsByUserIdUrl, { headers: tokenHeader })
            .pipe(
                tap(data => console.log()),
                catchError(this.handleError)
            );
    }

    getTransactionDetailsByDates(startDate: Date, endDate: Date): Observable<ITransactionDetails[]> {
        const tokenHeader = new HttpHeaders({ "Authorization": "Bearer " + localStorage.getItem("token") });

        return this.http.get<ITransactionDetails[]>(this.getTransactionDetailsByDatesUrl + startDate + "/" + endDate, { headers: tokenHeader })
            .pipe(
                tap(data => console.log()),
                catchError(this.handleError)
            );
    }

    getTransactionDetailsByReferenceNo(referenceNo: string): Observable<ITransactionDetails> {
        const tokenHeader = new HttpHeaders({ "Authorization": "Bearer " + localStorage.getItem("token") });

        return this.http.get<ITransactionDetails>(this.getTransactionDetailsByReferenceNoUrl + referenceNo, { headers: tokenHeader })
            .pipe(
                tap(data => console.log()),
                catchError(this.handleError)
            );
    }

    private handleError(err: HttpErrorResponse) {
        // in a real world app, we may send the server to some remote logging infrastructure
        // instead of just logging it to the console
        let errorMessage = '';
        if (err.error instanceof ErrorEvent) {
            // A client-side or network error occurred. Handle it accordingly.
            errorMessage = `An error occurred: ${err.error.message}`;
        } else {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
            errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
        }
        console.error(errorMessage);
        return throwError(errorMessage);
    }
}
