import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { GlobalService } from './global.service';
import { ITeams } from './interface/ITeams';
import { IUserType } from './interface/IUserType';

@Injectable({
    providedIn: 'root'
})

export class DefinitionService {
    private getUserTypesUrl: string = 'api/Definition/GetUserTypes';
    private getTeamsUrl: string = 'api/Definition/GetTeams';

    constructor(private http: HttpClient, private globalService: GlobalService) {
        this.getUserTypesUrl = globalService.baseUrl() + this.getUserTypesUrl;
        this.getTeamsUrl = globalService.baseUrl() + this.getTeamsUrl;
    }

    getUserTypes(): Observable<IUserType[]> {
        return this.http.get<IUserType[]>(this.getUserTypesUrl)
          .pipe(
            tap(data => console.log()),
            catchError(this.handleError)
          );
      }
    
      getTeams(): Observable<ITeams[]> {
        return this.http.get<ITeams[]>(this.getTeamsUrl)
          .pipe(
            tap(data => console.log()),
            catchError(this.handleError)
          );
      }

      private handleError(err: HttpErrorResponse) {
        // in a real world app, we may send the server to some remote logging infrastructure
        // instead of just logging it to the console
        let errorMessage = '';
        if (err.error instanceof ErrorEvent) {
          // A client-side or network error occurred. Handle it accordingly.
          errorMessage = `An error occurred: ${err.error.message}`;
        } else {
          // The backend returned an unsuccessful response code.
          // The response body may contain clues as to what went wrong,
          errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
        }
        console.error(errorMessage);
        return throwError(errorMessage);
      }
}