import { Component, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { ITransactionDetails } from 'src/app/shared/interface/ITransactionDetails';
import { TransactionService } from 'src/app/shared/transaction.service';

@Component({
  selector: 'app-my-transactions',
  templateUrl: './my-transactions.component.html',
  styleUrls: ['./my-transactions.component.scss']
})
export class MyTransactionsComponent implements OnInit {
  p: number = 1;
  transactionDetails: ITransactionDetails[] = []
  errorMessage: string = '';
  searchValue: string;
  showDateInput = false;
  startDate: Date;
  endDate: Date;
  isFilterSuccess: boolean = false;
  filterAmount = { count: 0 };
  showSpinner;


  constructor(private transactionService: TransactionService, private toastr: ToastrService, private ngxService: NgxUiLoaderService) { }

  ngOnInit(): void {
    this.showSpinner = true;
    this.transactionService.getTransactionDetailsByUserId().subscribe({
      next: transactionDetails => this.transactionDetails = this.onComplete(transactionDetails),
        error: err => this.errorMessage = err
    });
  }

  toggleDateInput(): any {
    this.showDateInput = !this.showDateInput;
  }

  filterByDate(): any {
    if (!this.startDate || !this.endDate) {
      this.toastr.error("Please fill out the dates to be filtered.", "Date Input Error");
    }
    else if (this.startDate > this.endDate) {
      this.toastr.error("End Date can't be greater then Start Date", "Date Input Error");
      this.startDate = null;
      this.endDate = null;
    }
    else {
      this.transactionDetails = null;
      this.showSpinner = true;
      this.transactionService.getTransactionDetailsByDates(this.startDate, this.endDate).subscribe({
        next: transactionDetails => this.transactionDetails = this.onFilterComplete(transactionDetails),
        error: err => this.errorMessage = err
      });
    }
  }

  onComplete(transactionDetails: ITransactionDetails[]): ITransactionDetails[] {
    this.filterAmount.count = transactionDetails.reduce((sum, current) => sum + current.amount, 0)
    this.showSpinner = false;
    return transactionDetails;
  }

  onFilterComplete(transactionDetails: ITransactionDetails[]): ITransactionDetails[] {
    this.filterAmount.count = transactionDetails.reduce((sum, current) => sum + current.amount, 0)
    this.isFilterSuccess = true;
    this.showSpinner = false;
    return transactionDetails;
  }

  resetTransactionDetails(): any {
    this.transactionDetails = null;
    this.showSpinner = true;
    this.startDate = null;
    this.endDate = null;
    this.isFilterSuccess = false;
    this.transactionService.getTransactionDetailsByUserId().subscribe({
      next: transactionDetails => this.transactionDetails = this.onComplete(transactionDetails),
      error: err => this.errorMessage = err
    });
  }

}
