import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';

import { ToastrService } from 'ngx-toastr';
import { NgxUiLoaderService } from 'ngx-ui-loader';
import { debounceTime } from 'rxjs/operators';


import { Constants } from 'src/app/shared/constants';
import { GlobalService } from 'src/app/shared/global.service';
import { ILogin } from 'src/app/shared/interface/ILogin';
import { IUser } from 'src/app/shared/interface/IUser';
import { LoginService } from 'src/app/shared/login.service';
import { UserService } from 'src/app/shared/user.service';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
    headerText: string = 'Find new local restaurants and independent home cooks';
    subHeaderText: string = 'Tired of the common restaurants you see? Try new local foods, homemade foods and more';
    loginForm: FormGroup;
    loginData: ILogin;
    constants: Constants;
    userNameMessage: string;
    passwordMessage: string;
    errorMessage: "";
    userNameVerificationMessage: string;

    constructor(private fb: FormBuilder, private globalService: GlobalService, private loginService: LoginService, private router: Router,
        private toastr: ToastrService, private userService: UserService, private ngxService: NgxUiLoaderService) {
        this.constants = new Constants();
        this.globalService.setCurrentPage("login");
    }

    @Output() isFormDirty: EventEmitter<boolean> = new EventEmitter<boolean>();

    private userNameValMessages = {
        required: "Please enter your username.",
        pattern: "Username is invalid."
    }

    private passwordValMessages = {
        required: "Please enter your password.",
    }

    ngOnInit(): void {
        if (this.globalService.isAuthenticated()) {
            this.router.navigate(['/home']);
        }

        this.loginForm = this.fb.group({
            userName: ['', [Validators.required, Validators.pattern(/^[a-zA-Z0-9_-]+$/)]],
            password: ['', [Validators.required]]
        });

        const userNameControl = this.loginForm.get(this.constants.userName);
        userNameControl.valueChanges.pipe(debounceTime(1000)).subscribe(
            value => {
                this.setMessage(userNameControl, this.constants.userName)
                this.verifyUserName(value)
            });

        const passwordControl = this.loginForm.get(this.constants.password);
        passwordControl.valueChanges.pipe(debounceTime(1000)).subscribe(
            value => this.setMessage(passwordControl, this.constants.password)
        );

        this.loginForm.valueChanges.subscribe(
            value => this.isFormDirty.emit(true)
        );
    }

    verifyUserName(value: string): any {
        this.userService.getActiveUserByUserName(value).subscribe({
            next: userDetail => this.userNameChecker(userDetail),
            error: err => this.errorMessage = err
        });
    }

    userNameChecker(userDetail: IUser) {
        this.userNameVerificationMessage = null;
        if (userDetail != null && userDetail.userId != null && userDetail.isActive == false) {
            if (userDetail.userType == 'Loader') {
                this.userNameVerificationMessage = "This Loader Account is not yet active. Please contact your Financer."
            }
            else if (userDetail.userType == 'Financer') {
                this.userNameVerificationMessage = "This Financer Account is not yet active. Please contact the system administrator"
            }
        }
    }

    setMessage(c: AbstractControl, validationType: string): void {
        if (validationType == this.constants.userName) {
            this.userNameMessage = "";
            if ((c.touched || c.dirty) && c.errors) {
                this.userNameMessage = Object.keys(c.errors).map(
                    key => this.userNameValMessages[key]).join(' ');
            }
        } else if (validationType == this.constants.password) {
            this.passwordMessage = "";
            if ((c.touched || c.dirty) && c.errors) {
                this.passwordMessage = Object.keys(c.errors).map(
                    key => this.passwordValMessages[key]).join(' ');
            }
        }
    }

    setLoginValue(loginCustomerForm: FormGroup): ILogin {
        return {
            userName: loginCustomerForm.get("userName").value,
            password: loginCustomerForm.get("password").value,
        };
    }

    loginUser(): void {
        this.ngxService.start();
        if (this.loginForm.valid) {
            if (this.loginForm.dirty) {
                this.loginService.loginUser(this.setLoginValue(this.loginForm)).subscribe({
                    next: (res: any) => {
                        localStorage.setItem('token', res.token);
                        localStorage.setItem('isAuthenticated', "true");
                        this.globalService.setUserLoggedIn(true);
                        this.userService.getUserProfile().subscribe({
                            next: user => this.setUserDetails(user),
                            error: err => console.log(err)
                        });
                        this.router.navigate(['/home']);
                        this.toastr.success("Welcome to Casino MS", "Login Successfully");
                    },
                    error: err => this.errorHandler(err)
                })
            }
        }
    }

    setUserDetails(user: IUser): void {
        localStorage.setItem('fullName', user.firstName + ' ' + user.lastName);
        localStorage.setItem('UserType', user.userType);
        this.ngxService.stop();
    }

    errorHandler(message: string) {
        if (message.includes("400")) {
            this.toastr.error("Incorrect Username or Password", "Authentication Failed");
        } else {
            console.log(message);
        }

        this.ngxService.stop();
    }
}
