import { Pipe, PipeTransform } from '@angular/core';
import { ITransactionDetails } from '../interface/ITransactionDetails';

@Pipe({
  name: 'searchFilter'
})
export class SearchFilterPipe implements PipeTransform {
  filteredTransactionDetails: ITransactionDetails[];
  cashIn: number;
  cashOut: number;

  transform(transactionDetails: ITransactionDetails[], searchValue: string, filterMetadata): ITransactionDetails[] {
    if (!transactionDetails || !searchValue) {
      return transactionDetails;
    }

    this.filteredTransactionDetails = transactionDetails.filter(transactionDetails =>
      transactionDetails.submittedBy?.toLocaleLowerCase().includes(searchValue?.toLocaleLowerCase()) ||
      transactionDetails.playerUserName?.toLocaleLowerCase().includes(searchValue?.toLocaleLowerCase()) ||
      transactionDetails.transactionType?.toLocaleLowerCase().includes(searchValue?.toLocaleLowerCase()) ||
      transactionDetails.referenceNo?.toLocaleLowerCase().includes(searchValue?.toLocaleLowerCase()) ||
      transactionDetails.amount?.toString().includes(searchValue)
    );

    this.cashIn = this.filteredTransactionDetails.filter(x => x.transactionType == "Cash-In").reduce((sum, current) => sum + current.amount, 0);
    this.cashOut = this.filteredTransactionDetails.filter(x => x.transactionType == "Cash-Out").reduce((sum, current) => sum + current.amount, 0);
    
    filterMetadata.count = this.cashIn - this.cashOut;

    return this.filteredTransactionDetails;
  }

}
